@use 'sass:math';

.container {
    margin: 0 auto;
    max-width: math.div(1000, 1200) * 100%;

    @media (max-width: 1199px){
        max-width: math.div(1000, 1200) * 100%;
    }

    &__navbar{
        margin: 0 auto;
        padding-left: 1.5rem;
        max-width: math.div(1000, 1200) * 100%;
        @media (max-width: 1199px){
            max-width: math.div(1000, 1200) * 100%;
        }
    }

}